import styles from "./page.module.css";

// Mock template data - will be replaced with actual store data
const mockTemplates = [
  {
    id: "1",
    name: "YouTube Shorts Template",
    description: "Vertical video template with dynamic captions",
    thumbnail: null,
    placeholderCount: 3,
    duration: 60,
    createdAt: new Date("2024-01-05"),
  },
  {
    id: "2",
    name: "TikTok Hook Template",
    description: "Attention-grabbing intro with text overlays",
    thumbnail: null,
    placeholderCount: 5,
    duration: 15,
    createdAt: new Date("2024-01-03"),
  },
  {
    id: "3",
    name: "Product Showcase",
    description: "Professional product reveal animation",
    thumbnail: null,
    placeholderCount: 4,
    duration: 30,
    createdAt: new Date("2024-01-01"),
  },
];

export default function TemplatesPage() {
  return (
    <div className={styles.container}>
      <header className={styles.header}>
        <div>
          <h1 className={styles.title}>Templates</h1>
          <p className={styles.subtitle}>
            Manage your automation templates
          </p>
        </div>
        <button className={styles.createBtn}>
          <span>+</span>
          <span>New Template</span>
        </button>
      </header>

      <div className={styles.grid}>
        {mockTemplates.map((template) => (
          <article key={template.id} className={styles.card}>
            <div className={styles.cardThumbnail}>
              <span className={styles.placeholderIcon}>üé¨</span>
            </div>
            <div className={styles.cardContent}>
              <h3 className={styles.cardTitle}>{template.name}</h3>
              <p className={styles.cardDescription}>{template.description}</p>
              <div className={styles.cardMeta}>
                <span className={styles.metaItem}>
                  üìù {template.placeholderCount} placeholders
                </span>
                <span className={styles.metaItem}>
                  ‚è±Ô∏è {template.duration}s
                </span>
              </div>
            </div>
            <div className={styles.cardActions}>
              <button className={styles.actionBtn} title="Use Template">
                ‚ñ∂Ô∏è Use
              </button>
              <button className={styles.actionBtn} title="Edit">
                ‚úèÔ∏è
              </button>
              <button className={styles.actionBtn} title="Delete">
                üóëÔ∏è
              </button>
            </div>
          </article>
        ))}

        {/* Empty state / Add new card */}
        <article className={styles.addCard}>
          <div className={styles.addCardContent}>
            <span className={styles.addIcon}>+</span>
            <span>Create Template</span>
            <p className={styles.addHint}>
              Create a video in OpenCut, then import it as a template
            </p>
          </div>
        </article>
      </div>
    </div>
  );
}
